<template>
  <div class="u-build">
    <component v-for="item in data"
      :is="item.layoutComponent"
      v-bind="item"
      :model="value"></Snippet>
  </div>
</template>

<script>
export default {
  name: 'Build',
  props: {
    data: {
      type: Array,
      default: () => []
    },
    plainData: true, //
    value: { // 
      type: Object,
      default: () => {
        return {}
      }
    }
    /*
      [
        //Snippet
        {
          name: //
          title: //
          tools: //
          fields: [] //
          layout: [] //
          layoutComponent 'Snippet' //Snippet
          boxComponent: 'Box'
        }
      ]
    */
  }
}
</script>

<style lang="less">

</style>



